@tailwind base;
@tailwind components;
@tailwind utilities;

@supports not ((-webkit-backdrop-filter: none) or (backdrop-filter: none)) {
  .menu-klfn {
    background-color: rgba(251, 230, 75, 0.6);
  }

  .modal-klfn {
    background-color: rgba(251, 230, 75, 0.6);
  }
}

.menu-open div:nth-child(1) {
  transform: rotate(45deg);
  top: -2px;
}

.menu-open div:nth-child(2) {
  width: 0%;
  opacity: 0;
}

.menu-open div:nth-child(3) {
  transform: rotate(-45deg);
  top: 32px;
}
